import _ from 'lodash';
import React, { Component } from 'react';
import styled, { keyframes } from 'styled-components';

import Logos from 'assets/images/InsurerLogos';
import './LoadingScreenV2.scss';

class LogoSlider extends Component {
  constructor(props) {
    super(props);

    const logos = _.map(Logos.small, logo => logo);
    const keyFrameLogo = keyframes`
      0%    { transform: translateX(0); }
      100%  { transform: translateX(calc(-84px * ${logos.length})); }
    `;

    const AnimatedContainer = styled.div`
      animation: ${keyFrameLogo} 5s linear infinite;
    `;

    this.state = {
      logos: _.map(Logos.small, logo => logo),
      AnimatedContainer,
    };

    this.renderLogos = this.renderLogos.bind(this);
  }

  componentWillUnmount() {
    clearInterval(this.interval);
  }

  renderLogos(key) {
    const { logos } = this.state;
    return _.map(logos, (logo, index) => (
      <img key={`loading-icon-${key}-${index}`} src={logo} alt={`loading-icon-${index}.svg`} />
    ));
  }

  render() {
    const { AnimatedContainer } = this.state;

    return (
      <AnimatedContainer
        className="logo-container"
      >
        {this.renderLogos(0)}
        {this.renderLogos(1)}
      </AnimatedContainer>
    );
  }
}

export default LogoSlider;
