/**
 * ChangeInitialFormData.js
 * This is the green pencil button that appears mainly on
 * the compare view which will render the Edit Detail forms
 * of each category.
 *
 * Users will be able to change that form through this component
 * and will do a callback to update any changes, eg for compare view.
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import document from 'global/document';
import _ from 'lodash';
import React, { Component } from 'react';
import {
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
} from 'reactstrap';
import PropTypes from 'prop-types';

import './ModalPopup.scss';

class ModalPopup extends Component {
  constructor(props) {
    super(props);

    this.state = {
      showModal: props.showModal,
    };

    this.setHiddenAuto = this.setHiddenAuto.bind(this);
  }

  componentDidMount() {
    const { showModal } = this.props;

    if (!showModal) {
      this.setHiddenAuto();
    }
  }

  // Toggle modal
  componentWillReceiveProps(nextProps) {
    const { showModal } = this.props;
    if (nextProps.showModal !== showModal) {
      this.setState({ showModal: nextProps.showModal });

      if (!nextProps.showModal) {
        this.setHiddenAuto();
      }
    }
  }

  componentWillUnmount() {
    clearInterval(this.setHiddenInterval);
  }


  setHiddenAuto() {
    clearInterval(this.setHiddenInterval);
    this.setHiddenInterval = setInterval(() => {
      if (!this.props.showModal && !_.includes(document.body.style.overflow, 'auto')) {
        document.body.style.overflow = 'hidden auto';
        clearInterval(this.setHiddenInterval);
      }
    }, 100);
  }

  render() {
    const { showModal } = this.state;
    const {
      closeModal,
      header,
      body,
      footer,
      className,
      backdropClassName,
      zIndex,
    } = this.props;

    return (
      <Modal
        isOpen={showModal}
        size="lg"
        className={`modal-popup-custom ${className}`}
        backdropClassName={backdropClassName}
        zIndex={zIndex}
      >
        {
          header &&
          <ModalHeader
            tag="h3"
            toggle={closeModal}
          >
            {header}
          </ModalHeader>
        }
        {
          body &&
          <ModalBody>
            {body}
          </ModalBody>
        }
        {
          footer &&
          <ModalFooter>
            {footer}
          </ModalFooter>
        }
      </Modal>
    );
  }
}

ModalPopup.defaultProps = {
  showModal: false,
  header: null,
  body: null,
  footer: null,
  className: null,
  backdropClassName: null,
  zIndex: 1050,
};

ModalPopup.propTypes = {
  showModal: PropTypes.bool,
  closeModal: PropTypes.func.isRequired,
  header: PropTypes.object,
  body: PropTypes.object,
  footer: PropTypes.object,
  className: PropTypes.string,
  backdropClassName: PropTypes.string,
  zIndex: PropTypes.number,
};

export default ModalPopup;
