/**
 * Checkout.js
 * This is the checkout view that will render the summary
 * of details and handle all the promo, cashback and stripe
 * APIs.
 *
 * There is 2 state to this view. Checkout or receipt. It will
 * toggle accordingly if charged/ahHocCharged.
 *
 * Upon successful payment, it will update the user data again
 * to check if any cashback is used.
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import React, { Component, Fragment } from 'react';
// import PropTypes from 'prop-types';

import LoadingScreenV2 from 'components/Templates/LoadingScreenV2/LoadingScreenV2';

const defaultMsg = 'Please try again.';

class APILoader extends Component {
  constructor(props) {
    super(props);

    this.state = {
      showLoading: false,
      showLoadingError: false,
      errorMsg: null,
      errorCallback: null,
    };

    this.handleAPI = this.handleAPI.bind(this);
    this.showLoading = this.showLoading.bind(this);
    this.showLoadingError = this.showLoadingError.bind(this);
  }

  async showLoading(show) {
    await new Promise((resolve) => { this.setState({ showLoading: show }, resolve); });
  }

  async showLoadingError(show) {
    await new Promise((resolve) => { this.setState({ showLoadingError: show }, resolve); });
  }

  async handleAPI(apiCallback, overwriteWarning = false, errorMsg = null, errorCallback = null) {
    try {
      clearInterval(this.showLoadigInterval);
      this.showLoadigInterval = setInterval(() => this.setState({
        showLoading: true,
      }), 300);

      const result = await apiCallback();

      clearInterval(this.showLoadigInterval);

      const showError = overwriteWarning ? false : !result;

      this.setState({
        showLoadingError: showError,
        showLoading: showError,
        errorMsg,
        errorCallback,
      });
      return result;
    } catch (error) {
      // eslint-disable-next-line
      console.log(error);
    }

    this.setState({
      showLoadingError: true,
      showLoading: true,
      errorMsg,
      errorCallback,
    });

    return null;
  }

  render() {
    const {
      showLoading,
      showLoadingError,
      errorMsg,
      errorCallback,
    } = this.state;

    return (
      <Fragment>
        <LoadingScreenV2
          isLoading={showLoading}
          isLoadingError={showLoadingError}
          message={<h5>{errorMsg || defaultMsg}</h5>}
          showLoading={this.showLoading}
          showLoadingError={this.showLoadingError}
          errorCallback={errorCallback}
          noLogos
        />
      </Fragment>
    );
  }
}

APILoader.defaultProps = {
};

APILoader.propTypes = {
};

export default APILoader;
