/**
 * LoadingScreen.js
 * Loading screen for anywhere use
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import React, { Component, Fragment } from 'react';
import PropTypes from 'prop-types';

import ModalPopup from 'components/Templates/ModalPopup/ModalPopup';
import SpinnerAnimation from 'components/Templates/SpinnerAnimation/SpinnerAnimation';
import Button from 'components/FormsV3/Button/Button';

import LogoSlider from './LogoSlider';

import './LoadingScreenV2.scss';

const timeOutTimer = 30000;

class LoadingScreenV2 extends Component {
  constructor(props) {
    super(props);

    this.state = {
      overwriteShowWarning: false,
    };

    this.closeLoading = this.closeLoading.bind(this);
    this.handleChange = this.handleChange.bind(this);
  }

  componentDidMount() {
    this.handleChange();
  }

  componentDidUpdate(prevProps) {
    const { isLoading } = this.props;

    if (isLoading !== prevProps.isLoading) {
      this.handleChange();
    }
  }

  handleChange() {
    const { isLoading } = this.props;
    if (isLoading) {
      this.timer = setTimeout(() => this.setState({ overwriteShowWarning: true }), timeOutTimer);
    }

    if (!isLoading) {
      clearTimeout(this.timer);
      this.setState({ overwriteShowWarning: false });
    }
  }

  async closeLoading() {
    const {
      showLoadingError,
      showLoading,
      errorCallback,
    } = this.props;

    this.setState({ overwriteShowWarning: false }, async () => {
      await showLoadingError(false);
      showLoading(false);

      if (errorCallback) {
        errorCallback();
      }
    });
  }

  render() {
    const { overwriteShowWarning } = this.state;
    const {
      isLoading,
      isLoadingError,
      message,
      noLogos,
    } = this.props;

    const loadingStyle = isLoading ? 'loading-screen-active' : '';

    return (
      <Fragment>
        <div
          className={`loading-screen-v2 ${loadingStyle}`}
        >
          {
            isLoading ?
              <Fragment>
                <SpinnerAnimation />
                {
                  !noLogos &&
                  <div className="bottom-container">
                    <p>Searching from esteemed partners...</p>
                    <LogoSlider />
                  </div>
                }
              </Fragment>
            : null
          }
        </div>
        <ModalPopup
          showModal={overwriteShowWarning || isLoadingError}
          closeModal={this.closeLoading}
          className="loading-modal"
          backdropClassName="blue-modal-backdrop"
          header={<p>An error has occured</p>}
          body={message}
          zIndex={1052}
          footer={
            <Button
              variant="medium-flat-green"
              height={48}
              width={162}
              onClick={this.closeLoading}
            >
              <p>Close</p>
            </Button>
          }
        />
      </Fragment>
    );
  }
}

LoadingScreenV2.defaultProps = {
  noLogos: false,
  errorCallback: null,
};

LoadingScreenV2.propTypes = {
  isLoading: PropTypes.bool.isRequired,
  isLoadingError: PropTypes.bool.isRequired,
  message: PropTypes.object.isRequired,
  showLoading: PropTypes.func.isRequired,
  showLoadingError: PropTypes.func.isRequired,
  noLogos: PropTypes.bool,
  errorCallback: PropTypes.func,
};

export default LoadingScreenV2;
